import React, { useContext } from 'react';

import { NavbarText } from "reactstrap";

import cn from "classnames"

import { ScenarioContext } from "../../contexts/ScenarioContext";

import TimerIcon from "../icons/TimerIcon"

import { formatAsMinSecs } from "../../utils"

const Timer = () => {
	const { started, paused, timers } = useContext(ScenarioContext)

	const scenTimer = timers.find(timer => timer.id === "scenario")

	return (
		<>
			{/* <NavbarText className="material-icons-outlined">
				{(paused || !started) ? "timer_off" : "timer"}
			</NavbarText> */}
			<TimerIcon running={paused || !started} />
			<NavbarText className={cn({"text-danger": scenTimer.expired})}>{formatAsMinSecs(scenTimer.timeRemaining)}</NavbarText>
		</>
	)
}

export default Timer;